.popup {
    position: fixed;
    display: flex;
    flex-direction: column;
    z-index: 2;
    width: 100vw;
    height: 100vh;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5); /* Black w/ opacity */
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s linear;
}
.popup .popup__wrapper {
    /*This div make the closing button responsive*/
    margin-top: 180px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.popup__form-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    max-width: 430px;
    min-height: 100%;
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    padding: 5% 10%;
    margin: 0;
}
.popup__form {
    max-width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
}
.popup__wrapper_type_confirm {
    position: relative;
    display: flex;
    flex-direction: column;
    max-width: 430px;
    height: 181px;
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    margin: 0;
}
.popup__wrapper_profile-image {
    height: 272px;
}
.popup__form_profile-image {
    height: 15px;
}
.popup__close-button {
    height: 32px;
    width: 32px;
    position: absolute;
    right: -35px;
    top: -35px;
    padding: 0;
    cursor: pointer;
    border: none;
    background-color: rgba(0, 0, 0, 0);
    background-image: url(../images/closeIcon.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    transition: 0.2s;
}
.popup__close-button:hover {
    opacity: 0.6;
}
.popup__title {
    font-family: "Inter", sans-serif;
    width: 330px;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
}
.popup__input {
    position: relative;
    border: none;
    width: 358px;
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    color: #000000;
    padding: 0;
    padding-top: 6.5px;
    padding-bottom: 6.5px;
    outline: none;

    text-indent: 5px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.popup__input:nth-last-child(2) {
    margin-bottom: 0;
}
.popup__line {
    border: none;
    margin-top: 13px;
    opacity: 0.2;
    border-bottom: 1px solid #000000;
    width: 358px;
    margin-bottom: 0;
}

.popup__submit {
    align-self: center;
    margin-top: 5%;
    width: 358px;
    height: 50.15px;
    background: #000000;
    border-radius: 2px;
    border: none;
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #ffffff;
    cursor: pointer;
    padding: 0;
    transition: 0.3s;
}
.popup__submit:hover {
    opacity: 0.8;
}
.popup__submit-button_inactive {
    opacity: 0.2;
    cursor: auto;
}
.popup__submit-button_inactive:hover {
    opacity: 0.2;
}

.popup_show {
    transition: 0.3s;
    visibility: visible;
    opacity: 1;
}
.popup__field {
    display: flex;
    flex-direction: column;
    margin-bottom: 13px;
}
.popup__input-error {
    margin: 5px 0 0 0;
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #ff0000;
}

.popup__input_type_error {
    border-bottom-color: red;
}

@media screen and (max-width: 600px) {
    .popup {
        width: 100%;
    }
    .popup__form-wrapper {
        padding: 2%;
    }
    .popup + .popup__wrapper {
        width: 282px;
        margin-top: 100px;
    }
    .popup__close-button {
        height: 20px;
        width: 20px;
        right: 0;
        margin-right: 9px;
        margin-bottom: 19px;
    }
    .popup__form {
        width: 282px;
        height: 322px;
        position: relative;
        row-gap: 20px;
    }
    .popup__title {
        font-size: 18px;
        width: 238px;
        max-height: 22px;
    }
    .popup__input {
        width: 238px;
        max-height: 14px;
        padding-bottom: 9px;
    }
    .popup__line {
        width: 238px;
        margin-top: 13px;
    }

    .popup__input:nth-last-child(2) {
        margin-bottom: 0;
    }
    .popup__submit {
        position: absolute;
        width: 238px;
        height: 46px;
        margin: 0;
        font-size: 14px;
        padding: 0;
        bottom: 30px;
    }
}
